
@import url("https://fonts.googleapis.com/css2?family=Inria+Serif:wght@300;400;700&display=swap");

body {
    overflow: hidden;
}

.skills-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Centraliza o conteúdo, como no Home */
    height: 100vh; /* Mantém o tamanho igual às outras páginas */
    background-color: #2A2A2A;
    color: white;
    font-family: "Inria Serif", serif;
}

/* Título principal */
.skills-title {
    font-family: "Inria Serif", serif;
    font-weight: bold;
    font-size: 112px;
    letter-spacing: 0%;
    margin-bottom: 10px;
    transition: transform 0.5s ease-in-out, color 0.5s ease-in-out;
}

.skills-title:hover {
    transform: scale(1.001);
    color: #FFD730;
}

/* Subtítulo */
.skills-subtitle {
    font-size: 1.2rem;
    margin-top: 15px;
    margin-bottom: 15px;
    font-weight: 400;
}

/* Descrição */
.skills-description {
    max-width: 1400px;
    font-size: 1.2rem;
    margin: 10px auto;
    line-height: 1.4;
    font-weight: lighter;
}

.skills-title:hover {
    transform: translateY(-5px) scale(1.15);
    color: #CDC033;
  }
  
  .skills-description, .skills-subtitle:hover {
    transform:scale(1.05);
  }
  

.skills-description strong {
    font-weight: bold;
    color: white;
}

/* Link para o LinkedIn */
.skills-linkedin {
    font-size: 1.2rem;
    margin-bottom: 20px;
}

.skills-linkedin a {
    color: #FFD700;
    text-decoration: none;
    font-weight: bold;
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    column-gap: 21px;
    row-gap: 12px;
    max-width: 900px;
    margin-top: 50px;
    justify-content: center;
    align-items: center;
}

/* Estilo dos ícones */
.skill-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: transform 0.3s ease-in-out;
}

.skill-item:hover {
    transform: scale(1.1);
    
}

.skill-icon {
    width: 130px;
    height: 130px;
    margin-bottom: 35px;
}

/* Ícones sociais ajustados */
/* Ícones sociais ajustados */
.social-icons {
    position: absolute;
    left: 20px; /* Mantém os ícones alinhados à esquerda */
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    gap: 10px;
    z-index: 10; /* Mantém acima do conteúdo */
}

.skills-container {
    position: relative; /* Garante que os ícones sociais sejam posicionados em relação a este container */
}

  
  .social-icons a img {
    width: 55px;
    height: 55px;
    opacity: 1;
    transition: transform 0.3s ease-in-out, opacity 0.3s;
  }
  
  .social-icons a img:hover {
    transform: scale(1.2);
    opacity: 1;
  }
  


/* Animação do sublinhado no LinkedIn */
.linkedin-link {
    position: relative;
    display: inline-block;
    color: #FFD700;
    text-decoration: none;
    transition: transform 0.3s ease-in-out, color 0.3s;
}

.linkedin-link::after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background: #FFD700;
    position: absolute;
    left: 0;
    bottom: -3px;
    transform: scaleX(0);
    transition: transform 0.3s ease-in-out;
    
}

.linkedin-link:hover::after {
    transform: scaleX(1);
}

.linkedin-link:hover {
    color: #ffeb75;
    transform: scale(1.1);
    cursor: url(https://cur.cursors-4u.net/cursors/cur-7/cur638.cur), auto !important;
}

@keyframes linkedinGlow {
    from {
        text-shadow: 0 0 5px #FFD700;
    }
    to {
        text-shadow: 0 0 10px #FFD700;
    }
}

.linkedin-link {
    animation: linkedinGlow 1.5s infinite alternate ease-in-out;
}

/* Wrapper para centralizar todo o conteúdo sem afetar os ícones sociais */
.content-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 100%;
    max-width: 1200px;
    padding: 20px;
    margin-bottom: 60px;
}

/* Retângulos no canto inferior esquerdo */
.corner-decor {
    position: fixed;
    bottom: 20px;
    left: 20px;
    z-index: 900;
}

.corner-decor img {
    width: 200px;
    height: auto;
    opacity: 0.8;
}

/* Retângulos no canto inferior esquerdo */
.corner-decor {
    position: fixed;
    bottom: 50px;
    left: 0px;
    z-index: 900;
}

.corner-decor img {
    width: 230px;
    height: auto;
    opacity: 0.8;
}

.wave-container2 {
    position: absolute;
    bottom: -10px; /* Reduzindo a posição inicial para descer um pouco */
    left: 0;
    width: 100%;
    overflow: hidden;
  }
  
  .wave-svg {
    width: 100%;
    opacity: 30%;
    height: auto;
    animation: waveMove 5s infinite ease-in-out;
  }
  
  /* Animação suave de subir e descer */
  @keyframes waveMove {
    0% { transform: translateY(0px); }
    50% { transform: translateY(15px); } /* Sobe um pouco */
    100% { transform: translateY(0px); } /* Volta para a posição inicial */
  }
  
  /* Transição suave nos textos */
.skills-subtitle,
.skills-description,
.skills-linkedin {
    transition: transform 0.3s ease-in-out, text-shadow 0.3s ease-in-out;
}

/* Efeito suave ao passar o mouse */
.skills-subtitle:hover,
.skills-linkedin:hover {
    text-shadow: 0 0 8px rgba(255, 255, 255, 0.2);
    transform: scale(1.03);
}


.skills-description:hover {
    transform: scale(1.09);
}