/* Base styles for all screens */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header {
  font-size: 1.5rem;
  text-align: center;
  padding: 20px;
}

.image {
  width: 100%;
  height: auto;
  max-width: 100%;
}

/* Ajustes para telas pequenas (smartphones e tablets) */
@media (max-width: 768px) {
  body {
    padding: 10px;
  }

  .container {
    flex-direction: column;
  }

  .header {
    font-size: 1rem;
  }

  .image {
    width: 100%;
    max-width: 100%;
  }

  /* Adiciona margens para maior conforto */
  .footer {
    font-size: 0.9rem;
    padding: 15px;
  }
}

/* Ajustes para o MacBook Air 13" (resolução 1440 x 900) */
@media (min-width: 1280px) and (max-width: 1440px) {
  body {
    font-size: 16px;
  }

  .container {
    max-width: 90%; /* Ajustado para 90% da tela */
    margin: 0 auto;
  }

  .header {
    font-size: 1.2rem; /* Ajuste mais suave */
  }

  .image {
    width: 80%; /* Melhor ajustado */
    height: auto;
  }
}

/* Ajustes para o MacBook Pro 14" (resolução 1512 x 982) */
@media (min-width: 1440px) and (max-width: 1512px) {
  body {
    font-size: 16px;
  }

  .container {
    max-width: 85%;
    margin: 0 auto;
  }

  .header {
    font-size: 1.4rem;
  }

  .image {
    width: 90%;
    height: auto;
  }
}

/* Ajustes para o MacBook Pro 16" (resolução 1728 x 1117) */
@media (min-width: 1512px) and (max-width: 1728px) {
  body {
    font-size: 17px;
  }

  .container {
    max-width: 85%;
    margin: 0 auto;
  }

  .header {
    font-size: 1.6rem;
  }

  .image {
    width: 100%;
    height: auto;
  }
}

/* Para telas maiores que 1728px, você pode ajustar ainda mais ou manter os estilos padrões */
@media (min-width: 1728px) {
  body {
    font-size: 18px;
  }

  .container {
    max-width: 90%;
    margin: 0 auto;
  }

  .header {
    font-size: 1.8rem;
  }

  .image {
    width: 100%;
    height: auto;
  }
}

/* Animação de fade-in na tela inicial */
.home-screen {
  opacity: 0;
  transition: opacity 0.7s ease; /* Transição suave para o fade-in */
}

.home-screen.fade-in {
  opacity: 1; /* Aparece gradualmente */
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Garante altura total da tela */
}

.content-container {
  flex: 1; /* Faz com que o conteúdo cresça e empurre o Footer para baixo */
}

.footer {
  text-align: center;
  padding: 20px;
  background-color: #222;
  color: #aaa;
  position: relative;
  bottom: 0;
  width: 100%;
}
